/* @import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;600;700;800;900&display=swap'); */

.Heading {
  padding: 30px 40px 10px;
  margin-bottom: 100px;
  position: relative;
  /* color: #000; */
  z-index: 100;
  font-size: 5rem;
  font-weight: 600;
  /* padding-left: 40px; */
  font-style: italic;
  padding-bottom: 40px;
  color: rgb(255, 255, 255);
  /* font-family: 'Cinzel', serif; */
  font-family: "IM Fell English SC", serif;
  display: inline;
}

.Heading::before {
  content: "";
  position: absolute;
  /* left: 7%;
  bottom: 16px; */
  width: 75%;
  height: 40px;
  transform: skew(-40deg) translateX(20px) translateY(50px);
  background: rgba(78, 248, 248, 0.432);
  z-index: -1;
}

.leftabout,
.rightabout,
.rightaboutrow,
.lefttaboutrow,
.rightmost,
.followmerow {
  min-height: calc(100vh - 40px);
  padding: 0%;
}
.rightabout,
.rightmost {
  background-color: #111;
}

.leftabout,
.bottomabout,
.About {
  /* background-color: #555ac0; */
  background-color: #1e847f;
}
.About {
  padding-left: 50px;
}

.bottomabout {
  min-height: 40px;
}

.rightaboutrow {
  padding-left: 30px;
}

.container-box {
  z-index: 100;
}
.About .row {
  margin: 0;
}

@media only screen and (max-width: 900px) {
  .leftabout {
    display: none;
  }
  .rightabout {
    flex: 11 1;
    padding-right: 0 !important;
  }
  .rightaboutrow {
    padding-right: 30px;
  }
}
@media only screen and (max-width: 650px) {
  .rightmost {
    display: none;
  }
  .About {
    padding-right: 20px;
  }
  .hrline.col {
    display: none;
  }
}
@media only screen and (max-width: 400px) {
  .rightaboutrow {
    padding: 0;
  }
}

.mypic {
  width: 300px;
  position: absolute;
  right: calc(66.7% - 110px);
  z-index: 10;
  margin-top: 20px;
}

.myPicBackground {
  width: 250px;
  height: 320px;
  /* background-color: #3a726f; */
  position: absolute;
  right: calc(66.7% + 20px);
  top: calc(50% - 250px);
}

.leftabout {
  padding-left: 100px !important;
}

@media only screen and (max-width: 1097px) {
  .mypic {
    width: 350px;
  }
  .myPicBackground {
    width: 270px;
    height: 350px;
  }
}


.myname {
  font-size: 3rem;
  color: #97fffc;
  font-weight: 400;
  /* letter-spacing: 5px; */
  font-family: "Josefin Sans", sans-serif;
}

.kaaaam {
  font-size: 1.5rem;
  font-weight: 700;
  color: #97fffc;
  font-family: "Josefin Slab", serif;
}

.title-profile {
  width: 230px !important;
}

.hrline {
  width: calc(100% - 300px);
}

.bottomline::after,
.hrline::after {
  content: "";
  display: inline-block;
  height: 0.5em;
  vertical-align: text-bottom;
  width: 100%;
  margin-right: -100%;
  /* margin-left: 10px; */
  border-top: 2px solid #2ebdb6;
  text-align: end;
}

.bottomline::after {
  border-top: 4px solid #2ebdb6;
}
.box {
  width: 80px;
  height: 80px;
  /* background-color: #1177ff; */
  background-color: #1e8580;
  position: absolute;
  z-index: 1;
  /* margin-bottom: 100px; */
  top: calc(50% - 230px);
}

.info {
  font-size: 0.95rem;
  text-align: justify;
}

.details {
  z-index: 10;
  padding: 0 30px 0 42px;
}

.rightabout {
  padding-right: 50px;
}

.img-sq,
.img-h {
  display: none;
  margin: auto;
  padding: 50px 0 20px;
}

@media only screen and (max-width: 1097px) {
  .box {
    top: calc(50% - 260px);
  }
}

@media only screen and (max-width: 900px) {
  .box {
    display: none;
  }
  .info {
    font-size: 1rem;
  }
  .img-h {
    width: 100%;
    display: block;
  }
  .details {
    padding: 0;
  }
}

@media only screen and (max-width: 700px) {
  .myname {
    font-size: 2rem;
  }
  .kaaaam {
    font-size: 1.3rem;
    font-weight: 700;
  }
  .hrline {
    width: 100%;
  }
  .info {
    font-size: 0.95rem;
    text-align: justify;
  }
}
@media only screen and (max-width: 500px) {
  .info {
    font-size: 0.9rem;
    text-align: justify;
  }
}

.followmelinks {
  /* text-align: end; */
  padding: 10px 0;
  font-size: 1.2rem;
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.followmebox {
  padding: 60px 0;
  width: 60px;
  background-color: #1e847f;
}

.Navbar {
  width: 160px;
  padding: 5px;
  height: 100vh;
  position: fixed;
  z-index: 100;
}
.row {
  margin: 0 !important;
}
.navlinks {
  height: 30px;
  padding: 20px 10px 30px;
  margin: 5px 0;
  display: flex;
  transition: ease-in-out 0.2s;
  border-radius: 1px;
  align-items: center;
  font-weight: 600;
  /* border: 1px solid #1e847f; */
  width: 59px;
  /* padding: 10px; */

}
.hover-navlink-hide {
  font-size: 2rem;
}

.hover-navlink {
  display: none;
  transition: none;
  padding: 10px 0 0 10px;
}

.navlinks:hover{background-color:#00fff2;color:#111;width:170px}.navlinks:hover .hover-navlink{display:inline;font-size:1rem}@media only screen and (max-width: 800px){.Navbar{display:none}.About{background-color:#111;padding-left:20px;padding-top:20px}.bottomabout{background-color:#111}}
